html {
    --penguin-gray: #393F36;
    --penguin-light: #AFC6EC;
    --penguin-normal: #93ACD8;
    --penguin-dark: #778DB3;

    --linux-1: #DD6C8E;
    --linux-2: #D66385;
    --linux-3: #C05280;
    --linux-4: #BF517F;
    --linux-5: #9B3B77;
    --linux-6: #642460;
    --linux-7: #4B1A4F;
    --linux-8: #4A194E;
    --linux-9: #371144;
    --linux-10: #24063E;
}

#background {
    background: linear-gradient(var(--linux-2), var(--linux-9));
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: -4rem;
}

.circle {
    box-shadow: -1rem 1rem 1rem rgba(0, 0, 0, 0.1);    
}

.App {
    height: 100vh;
    overflow-x: hidden;
    position: relative;
}

.App .container {
    max-width: 1440px;
    margin: 0 auto;
    position: relative;
    margin-top: 4rem;
}

.App .container > div {
    flex-direction: column;
}

.App .container > div > div {
    margin: 0;
}

#logo {
    position: fixed;
    right: 0;
    left: 0;
    text-align: center;
    color: white;
    top: 0;
    letter-spacing: 0.25rem;
    font-weight: 100;
    font-size: 48px;
    z-index: 0;
}

#last-date {
    position: absolute;
    bottom: 1rem;
    left: 0;
    right: 0;
    text-align: center;
    color: var(--linux-10);
    font-size: 14px;
    opacity: 0.5;
}

#info {
    color: var(--linux-10);
    font-size: 22px;
    margin-left: 22px;
    font-weight: 500;
}

#email, #name {
    text-align: right;
}

#email {
    font-size: 14px;
}

.me {
    flex: 0 0 64px;
    width: 64px;
    height: 64px;
    margin: 0;
    display: block;
    border-radius: 50%;
    overflow: hidden;
}

.glass {
    /*animation: appear .7s normal;*/
    position: relative;
    display: block;
    margin: 0 auto;
    border-radius: 32px;
    border: 2px solid rgba(255, 255, 255, 0.05);
    background-color: rgba(175, 198, 236, 0.1);
    backdrop-filter: blur(16px);
    box-shadow: 0px 4px 2rem rgba(119, 141, 179, 0.2);
    padding: 1rem;
    width: 300px;
    min-height: 165px;
    z-index: 1;
    margin-bottom: 1rem;
    transform-origin: center;
}

.glass.white {
    background-color: rgba(255, 255, 255, 0.8);
    box-shadow: 0px 4px 2rem rgba(0, 0, 0, 0.2);
}

.glass > .glass.white {
    min-height: 400px;
    position: absolute;
    bottom: -1rem;
    left: 0;
    right: 0;
    animation: none;
    background-color: rgba(255, 255, 255, 0.5);
}

.glass.glass-container {
    min-height: 600px;
}

.glass.glass-container strong {
    font-size: 18px;
    margin-top: 8px;
    margin-bottom: 8px;
    display: inline-block;
}

.glass.glass-container p,
.glass.glass-container span,
.glass.glass-container strong,
.glass.glass-container h2,
.glass.white p,
.glass.white span,
.glass.white h2 {
    color: var(--linux-10);
}

.glass.auto {
    min-height: unset;
    height: auto;
}
.glass.one-line {
    min-height: unset;
    height: 128px;
}

.glass h2 {
    margin: 0;
    color: var(--linux-10);
    font-size: 22px;
}

.glass-button {
    outline: none;
    border-radius: 32px;
    padding: 1rem 1.5rem;
    border: 1px solid var(--linux-9);
    background-color: rgba(36, 6, 62, 0.9);
    backdrop-filter: blur(16px);
    color: white;
    font-size: 16px;
    box-shadow: -2px 4px 4px rgba(0, 0, 0, 0.5);
    cursor: pointer;
}
.glass-button:active {
    box-shadow: -2px 4px 4px rgba(0, 0, 0, 0.2);
    margin-top: 2px;
    margin-right: 1px;
}

.glass.white h2 {
    margin-bottom: 2rem;
}

.clock .glass {
    padding: 1rem;
    margin: 8px;
    width: 96px;
    text-align: center;
    height: 104px;
    min-height: 32px;
    color: rgba(255, 255, 255, 0.9);
    font-size: 48px;
    background-color: rgba(255, 255, 255, 0.2);
    border: 2px solid rgba(255, 255, 255, 0.1);
    box-shadow: -1px 2px 4px rgba(0, 0, 0, 0.1);
    margin-top: 0;
}

.clock .glass:last-child {
    width: 32px;
    height: 36px;
    font-size: 18px;
    padding: 4px;
    border-radius: 8px;
    margin-top: 4rem;
}

.clock > div {
    align-items: center;
    justify-content: flex-start;
}

.pill {
    color: var(--linux-7);
    box-shadow: -1px 2px 4px rgba(0, 0, 0, 0.1);
    display: inline-block;
    background-color: rgba(221, 108, 142, 0.1);
    backdrop-filter: blur(16px);
    padding: 0.5rem 1rem;
    border-radius: 32px;
    margin-bottom: 1rem;
}

.glass.white img {
    display: block;
    width: calc(100% + 6rem);
    position: relative;
    left: -3rem;
    height: auto;
    border-radius: 32px;
    overflow: hidden;
    border: 1px solid rgba(0, 0, 0, 0.2);
    opacity: 0.95;
    box-shadow: -2px 4px 4px rgba(0, 0, 0, 0.2);
    margin-top: 1rem;
    margin-bottom: 1rem;
}

#user-input {
    background: transparent;
    outline: none;
    border: none;
    padding: 4px;
    font-size: 32px;
    width: 2rem;
    color: var(--penguin-light);
    border-bottom: 1px solid var(--penguin-light);
}

.word {
    font-size: 22px;
}

::placeholder {
    color: var(--penguin-light);
}

.overflow-text {
    position: absolute;
    color: white;
    font-size: 32px;
    font-weight: 500;
    overflow: hidden;
    height: 48px;
    text-shadow: -8px 4px 4px rgba(36, 6, 62, 0.3);

    display: flex;
    flex-direction: row;
}

.overflow-text .word:nth-of-type(2n + 1) {
    color: var(--linux-9);
    padding: 0 2px;
}

.overflow-text.bg {
    overflow: visible;
    text-shadow: none;
}
.overflow-text.bg.on-right .word:first-child {
    display: inline-block;
    margin-left: 8px;
    min-width: 280px;
    box-shadow: -8px 8px 4px rgba(36, 6, 62, 0.2);
    backdrop-filter: blur(16px);
}
.overflow-text a {
    display: inline-block;
}
.overflow-text.bg .word:nth-of-type(2n + 1) {
    background-color: rgba(175, 198, 236, 0.3);
}
.overflow-text.bg .word:nth-of-type(2n) {
    background-color: rgba(36, 6, 62, 0.3);
}

.overflow-text a {
    text-decoration: none;
}


.overflow-text .word:nth-of-type(2n) {
    color: var(--penguin-light);
    padding: 0 2px;
}
.on-left .word:last-child {
    border-top-right-radius: 22px;
    border-bottom-right-radius: 22px;
    padding-right: 16px;
}
.on-right .word:first-child {
    border-top-left-radius: 22px;
    border-bottom-left-radius: 22px;
    padding-left: 16px;
}
.on-left .word:first-child {
    padding-left: 16px;
}
.on-right .word:last-child {
    padding-right: 16px;
}
.overflow-text {
    flex-wrap: nowrap;
    overflow: visible;
}
.on-left {
    left: -24px;
}
.on-right {
    left: 6rem;
}

.overflow-text {
    top: 33%;
}
.overflow-text + .overflow-text {
    top: 66%;
}

@keyframes appear {
    0% {
        opacity: 0.5;
        transform: scale(0.8);
    }
    100% {
        transform: scale(1);
        opacity: 1;
    }
}

@media(min-width: 768px) {
    .App .container {
        margin-top: 4rem;
    }

    .glass {
        width: 320px;
        min-height: 256px;
        padding: 1.5rem;
    }
    .word {
        font-size: 28px;
    }
    .glass h2 {
        font-size: 22px;
    }
    .on-left {
        left: unset;
        right: 7rem;
    }
    .on-right {
        left: 7rem;
    }
    .me {
        flex: 0 0 72px;
        width: 72px;
        height: 72px;
    }
}

@media(min-width: 1024px) {
    .App .container {
        margin-top: 8rem;
    }

    .glass {
        width: 420px;
        padding: 2rem;
    }
    .word {
        font-size: 32px;
    }
    .glass h2 {
        font-size: 26px;
    }
    .on-left {
        right: 9rem;
    }
    .on-right {
        left: 9rem;
    }
}

@media(min-width: 1440px) {
    
    .App .container > div {
        flex-direction: row;
    }

    .App .container > div > div {
        margin: 1rem;
    }

}